import { REQUEST_SUCCEEDED_CODE, TitleColor, } from "@/common/constants";
import { request, getWeatherInfo } from "@/utils/request";
import { GetNowDate } from "@/utils/dateUtils";
import { changeUnreadMessageSum } from "@/utils/tabBarBadgeUtils";
const Keywords = {
    'sunny': /晴/,
    'shower': /阵雨/,
    'thunder-shower': /雷阵雨/,
    'overcast': /阴/,
    'cloudy': /多云/,
    'rainy-storm': /暴雨/,
    'raniy-large': /大雨/,
    'raniy-lightning': /雷|电/,
    'raniy-mid': /中雨/,
    'raniy-small': /小雨/,
    'snow-small': /小雪|雪/,
    'snow-mid': /中雪/,
    'sleet': /雨夹雪/,
    'snow-large': /大雪/,
    'fog': /雾/,
    'smog': /霾/
};
/**
 * bugu 布咕界面
 * @description 布咕界面-用于展示一些活动
 * @Author: 穆兰
 * @Date: 2021/12/27
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/17
 */
export default {
    data() {
        return {
            nbTitle: "布咕",
            backgroundColor: "#fff",
            frontColor: TitleColor,
            bannerList: [],
            weatherResult: {
                forecast: [
                    { type: 'N/A' }
                ],
                wendu: 'N/A',
                ganmao: '',
                city: '武汉'
            },
            helloText: "",
            timeNow: "",
            weatherIcon: "",
        };
    },
    onShow() {
        changeUnreadMessageSum();
    },
    methods: {
        /**
         * @description 点击签到抽奖
         */
        onSignCardClick() {
            uni.navigateTo({
                url: '/pages/bugu-secondary-page/sign-in-lottery/sign-in-lottery',
            });
        },
        /**
         * @description 点击互动盒子
         */
        onBoxCardClick() {
            uni.navigateTo({
                url: '/pages/bugu-secondary-page/blind-box/blind-box',
            });
        }
    },
    async mounted() {
        console.log(this);
        this.bannerList = await getBannerList();
        this.helloText = getHellowText();
        this.timeNow = GetNowDate().MonthAndDay;
        let weatherInfo = await getWeather();
        if (weatherInfo) {
            this.weatherResult = weatherInfo.weatherResult;
            this.weatherIcon = weatherInfo.weatherIcon;
        }
    },
};
/**
 * @description 获取天气信息
 * @returns
 */
async function getWeather() {
    // 临时天气api
    let res = await getWeatherInfo();
    console.log("getWeatherInfo", res.data);
    if (res.data.desc === "OK") {
        let weatherResult = res.data.data;
        let weatherIcon = "";
        for (let weather in Keywords)
            if (Keywords[weather].test(weatherResult.forecast[0].type))
                weatherIcon = weather;
        return {
            weatherResult,
            weatherIcon,
        };
    }
}
/**
 * @description 获取轮播图列表
 * @returns {Promise<BannerItem[]>}
 */
async function getBannerList() {
    let res = await request({
        data: {
            method: "GET",
            group: "banner",
            action: "list",
            data: {},
            header: {
                "content-type": "application/x-www-form-urlencoded",
            },
        },
    });
    if (res.data.code === REQUEST_SUCCEEDED_CODE) {
        console.log("getBannerList", res.data);
        return res.data.data;
    }
    else {
        console.log("getBannerList", res.data);
        return [];
    }
}
/**
 *
 * @description 获取欢迎语
 * @returns {string}
 */
function getHellowText() {
    let date = new Date();
    if (date.getHours() >= 0 && date.getHours() < 12) {
        return "Good morning";
    }
    else if (date.getHours() >= 12 && date.getHours() < 18) {
        return "Good afternoon";
    }
    else {
        return "Good evening";
    }
}
